/* Overall parallax container */
.background-parallax {
    position: relative;
    height: auto; /* Enough height for a long scroll */
    overflow: hidden;
}

/* Parallax layers configuration */
.parallax-layer {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    background-size: cover;
    background-position: center;
    z-index: -1;
    transform: translateY(0); /* Initialize position */
    transition: transform 0.1s ease-out; /* Optional: Adds a bit of smoothness */
}

/* Layer-specific backgrounds and z-index */
.layer1 {
    background-image: url('../assets/images/image1.png');
    z-index: -5;
}

.layer2 {
    background-image: url('../assets/images/image2.png');
    z-index: -4;
}

.layer3 {
    background-image: url('../assets/images/image3.png');
    z-index: -3;
}

.layer4 {
    background-image: url('../assets/images/image4.gif');
    z-index: -2;
}

.layer5 {
    background-image: url('../assets/images/image5.png');
    z-index: -1;
}

/* Content that appears on top of the layers */

.content {
    position: relative;
    z-index: 1;
    text-align: center;
    color: #00132f;
    background-color: #00132f;
    margin-top: 100vh; /* Content starts after the parallax */
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
    /* overflow: scroll; */
}

